import React from "react";
import "../../style/misc/steps.css";
import StepOne from "../icons/steps/stepOne";
import StepTwo from "../icons/steps/stepTwo";
import StepThree from "../icons/steps/stepThree";

// import step from "../images/steps/steps-1.svg";
// import stepTwo from "../images/steps/steps-2.svg";
// import stepThree from "../images/steps/steps-3.svg";

const Stepts = () => {
  return (
    <div className="container-steps">
      <div className="bn-primary-color" />
      <h2>Sabes sobre los beneficios de la adopcin?</h2>
      <div className="steps">
        <div className="container-step">
          <StepOne />
          {/* <img src={stepOne} alt="Mujer abrazando un loro" /> */}
          <div className="step">
            <h3>Salvar Vidas</h3>
            <p>
              Cada mascota adoptada en BroPets significa una vida rescatada y
              una oportunidad de un futuro mejor.
            </p>
          </div>
        </div>
        <div className="container-step">
          <StepThree />
          {/* <img src={stepThree} alt="Hombre sosteniendo un conejo" /> */}
          <div className="step">
            <h3>Responsabilidad social</h3>
            <p>
              Al adoptar una mascota en lugar de comprar una, ests
              contribuyendo a la causa de rescate y adopcin de mascotas,
              promoviendo la responsabilidad social y ayudando a crear
              conciencia sobre la importancia de la adopcin.
            </p>
          </div>
        </div>
        <div className="container-step">
          <StepTwo />
          {/* <img src={stepTwo} alt="Mujer abrazando a un gato" /> */}
          <div className="step">
            <h3>Amor incondicional</h3>
            <p>
              Las mascotas adoptadas son compaeros leales y amorosos que te
              brindarn alegra y compaa todos los das.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Stepts;
